<template>
  <div class="groupPage">
    <div class="title" ref="title">
      <i class="iconfont" @click="back">&#xe6b6;</i>
      <h1>{{group}}</h1>
    </div>
    <div class="banner" :style="{'background-image':`url(${img})`}" ref="banner"></div>
    <div class="wrapper" ref=wrapper>
      <div class="content">
        <div class="foodBox"
          v-for="(item, index) in list" :key="index"
          @click="addFoodData(item)"
        >
          <div class="left">
            <img v-lazy="item.img" alt="">
          </div>
          <div class="right">
            <i class="iconfont">&#xe610;</i>
            <h3>{{item.foodName}}</h3>
            <div class="material">
              <span>食材：</span>
              <p>
                <span v-for="(mItem, index) in item.material" :key="index">
                  {{index==item.material.length-1?`${mItem.name}`:`${mItem.name}、`}}
                </span>
              </p>
            </div>
            <div class="step">
              <p> <i class="iconfont">&#xe67d;</i><span>{{item.look}}</span></p>
              <p><i class="iconfont">&#xe7ce;</i><span>{{item.collect}}</span></p>
              <p><i class="iconfont">&#xe60f;</i><span>{{item.share}}</span></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Bs from 'better-scroll'
import { getFoodByGroup } from 'api/api'
import { mapMutations } from 'vuex'
import { handleDataFood } from '../../utils/handleData'
export default {
  data () {
    return {
      group: '',
      img: '',
      list: []
    }
  },
  methods: {
    ...mapMutations(['addFoodData']),
    // 返回
    back () {
      this.$router.go(-1)
    },
    initBs () {
      const wrapper = this.$refs.wrapper
      /* eslint-disable no-new */
      this.Bs = new Bs(wrapper, { probeType: 3, click: true })
      const bHeight = this.$refs.banner.clientHeight
      const tHeight = this.$refs.title.clientHeight
      this.Bs.on('scroll', ({ y }) => {
        if (!this.$refs.banner) { return false }
        if (y >= 0) {
          const precent = 1 + y / bHeight
          this.$refs.banner.style.transform = `scale(${precent})`
        } else {
          if (Math.abs(y) >= (bHeight - tHeight)) {
            this.$refs.banner.style.zIndex = '1'
            this.$refs.banner.style.paddingTop = '.88rem'
          } else {
            this.$refs.banner.style.zIndex = '0'
            this.$refs.banner.style.paddingTop = '4rem'
          }
        }
      })
    },
    // 获取初始化数据
    getInitData () {
      getFoodByGroup(this.group).then((res) => {
        const data = handleDataFood(res.data, this.baseUrl)
        this.list = data
        this.$nextTick(() => {
          this.initBs()
        })
      })
    }
  },
  mounted () {
    this.group = this.$route.query.group
    this.img = this.$route.query.img
    this.getInitData()
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.groupPage{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  background: #f9f9f9;
  .title{
    height: .88rem;
    line-height: .88rem;
    text-align: center;
    color: #333333;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    h1{
      font-size: .34rem;
    }
    .iconfont{
      font-size: .34rem;
      position: absolute;
      top: 0;
      left: .06rem;
    }
  }
  .banner{
    width: 100%;
    padding-top: 4rem;
    background-size: cover;
    position: relative;
    top: 0;
  }
  .wrapper{
    position: fixed;
    top: 4rem;
    bottom: 0;
    left: 0;
    right: 0;
    .content{
      padding: .2rem 0;
      background: #f9f9f9;
      .foodBox{
        padding: .2rem .4rem;
        display: flex;
        align-items: center;
        .left{
          img{
            width: 1.6rem;
            height: 1.6rem;
            margin-right: 0.24rem;
            border-radius: .12rem;
          }
        }
        .right{
          flex: 1;
          height: 1.6rem;
          position: relative;
          >.iconfont{
            height: .4rem;
            font-size: .3rem;
            line-height: .4rem;
            color: @orange2;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
          }
          h3{
            font-size: .36rem;
            line-height: 0.48rem;
            margin: 0 0 .2rem;
            color: #333;
            width: 4rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          .material{
            display: flex;
            margin-bottom: .05rem;
            font-size: .28rem;
            line-height: .4rem;
            >span{
              color: @orange;
              font-size: .28rem;
              line-height: .4rem;
            }
            p{
              width: 3.6rem;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
          .step{
            font-size: .28rem;
            line-height: .4rem;
            display: flex;
            justify-content: space-between;
            width: 3.6rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            p{
              color: @gray;
              .iconfont{
                font-size: .28rem;
                line-height: .4rem;
                margin-right: .05rem;
              }
            }
          }
        }
      }
      .foodBox:not(:first-child){
        border-top: 1px solid #dcdcdc;
      }
    }
  }

}
</style>
